:root {
  --select-border: #777;
  --select-focus: blue;
  --select-arrow: var(--select-border);
  --select-width--:10rem;
  --card-width:280px;
  font-size: 16px;
  --box-shadow: 0 0 10px 1px rgba(0,0,0,0.15);
}

.light-mode{
  --Text--:hsl(200, 15%, 8%);
  --Input--:hsl(0, 0%, 52%);
  --Background--:hsl(0, 0%, 98%);
  --Elements--:hsl(0, 0%, 100%);
}
.dark-mode{
  --Elements--:hsl(209, 23%, 22%);
  --Background--:hsl(207, 26%, 17%);
  --Text--:hsl(0, 0%, 100%);
  --Input--:hsl(0, 0%, 100%);
}

  /* body{
    font-size: 1rem;
  } */

a{
  text-decoration: none;
  color: inherit;
}

header {
  background-color: var(--Elements--);
  box-shadow: var(--box-shadow);
  margin-bottom: 10px;
}
.App{
  background-color: var(--Background--);
  min-height: 100vh;
  color:var(--Text--);
  font-size: 16px;
}

header, main{
  padding-left: 5vw;
  padding-right: 5vw;
 
}
main{
  padding-bottom: 5rem;
}
.header{
  display: flex;
  justify-content: space-between;
  align-items: center;

}
.header > span{
  line-height: 24px;
  vertical-align: middle;
}
.toolbar{
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  
}

.searchBar{
  color: var(--Light-Mode-Input--);
  margin: 3rem 0;
}
input[type="text"]{
  box-shadow: 2px 2px 5px 2px rgba(0,0,0,0.05);
  border: none;
  padding: 1.25rem 1.25rem 1.5rem 5rem;
  border-radius: 0.15rem;
  min-width: max(30vw, var(--card-width));
  background: var(--Elements--);
  color: var(--Input--);
  height: 1rem;
  font-size: 1rem;
}
.custom-select{
  position: relative;
  background: none;
  border: none;
  border-radius: 0.25em;
  box-shadow: 0 0 5px 5px rgba(0,0,0,0.05);
  text-align: left;
  background-color: var(--Elements--);
  color: var(--Text--);
  padding: 0;
  margin: auto 0 3rem 0;
}
.selectTop {
  width: var(--select-width--);
  padding: 1.25rem;
  cursor: pointer;
  display: grid;
  grid-template-areas: "select";
  align-items: center;
}
.selectTop::after {
  position: absolute;
  content: "";
  width: 0.8em;
  height: 0.5em;
  background-color: var(--select-arrow);
  clip-path: polygon(100% 0%, 0 0%, 50% 100%);
  justify-self: end;
}
.selectBottom{
  background: inherit;
  box-shadow: inherit;
  border-radius: inherit;
  width: calc(var(--select-width--) + 2.5rem);
  
  position: absolute;
  top: 70px;
}
.selectBottom ul{
  list-style: none;
  padding: 0;
  margin: 0;
  border-radius: inherit;
}
.selectBottom li{
  padding: 0.5rem 1.5rem;
  border-radius: inherit;
  cursor: pointer;
}
.selectBottom li:hover{
  background-color: #777;
  color: white;
}

/* .toolbar > *{
  font-size: 2rem;
} */
.search-icon {
  position: absolute;
  padding-top: 1.3rem;
  padding-left: 2.5rem;
  font-size: 1.25rem;
}
.countries{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 1rem;

}
.countries a{
  margin-bottom: 1rem;
}
.card{
  width: var(--card-width);
  box-shadow: var(--box-shadow);
  border-radius: 0.5rem;
  height: 100%;
  margin-bottom: 1rem;
  background: var(--Elements--);
}

.empty-card{
  width: var(--card-width);
}
.card img{
  width: 100%;
  height: 100%;
  object-fit: fill;
  border-radius: 0.5rem 0.5rem 0 0;
}

.card > .imgContainer{
  width: 100%;
  aspect-ratio: 3 / 2;
}
.textContainer{
  margin: 1rem 1rem 2.5rem 1.5rem;
}
.textContainer p{
  margin: 0.5rem 0;
}
.textContainer span{
  font-weight: 600;
}
.themeSwitcher{
  cursor: pointer;
  font-size: max(1.5vw, 1rem);
  font-weight: 600;
}
.dark-symbol{
  font-size: 1.5rem;
  padding-left: 1rem;
  rotate: -220deg;

}

.back-button{
  margin: 3rem auto;
  padding: 0.5rem 3rem 0.5rem 2rem;
  background-color: var(--Elements--);
  color: var(--Text--);
  border: 0;
  font-size: 1rem;
  box-shadow: var(--box-shadow);
  border-radius: 0.25rem;

}
.flag{
  width: max(35vw, 300px);
  align-self: flex-start;
  flex-grow: 1;
}
.flag img{
  width: 100%;
  aspect-ratio: 3 / 2;
  box-shadow: var(--box-shadow);
}
.details{
  min-width: 40vw;
  flex-grow: 1;
}
.details div div{
  flex-grow: 1;
  width: max(15vw, 225px);
}
.flex{
  display: flex;
}
.align-center{
  align-items: center;
}
.g-10{
  gap: 5rem ;
}
.g-2{
  gap:2rem
}
.wrap{
  flex-wrap: wrap;
}
.sp-bw{
  justify-content: space-between;
}
.bold{
  font-weight: 700;
}
.back-icon{
  font-weight: 700;
  padding-right: 1rem;
  font-size: inherit;
}
h2{
  font-size: 2rem;
}
.border-button{
  background-color: var(--Elements--);
  color: var(--Text--);
  display: inline-block;
  box-shadow: var(--box-shadow);
  border-radius: 0.25rem;
  padding: 0.25rem 1rem;
  margin: 0.5rem;

}
h1{
  font-size: max(2vw, 1rem);
}
